.clearfix:before,
.clearfix:after {
  clear: both;
  content: " ";
  display: table;
}
html {
  background: #343434;
  box-sizing: border-box;
  color: #343434;
  font: 400 1em / 1.5 "Open Sans", sans-serif;
  overflow-wrap: break-word;
  scroll-behavior: smooth;
  word-wrap: break-word;
  word-spacing: inherit;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  background: #ffffff;
  box-shadow: 0 0 1px 0 #fffeea;
  margin: 0 auto;
  max-width: 53.333333333em;
  padding: 1em;
}
img,
figure,
video {
  max-width: 100%;
}
.hyphenate {
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
}
h1,
h2,
h3 {
  font: 400 2.369em / 1 "Exo 2", monospace;
}
h2 {
  font-size: 1.777em;
}
h3 {
  font-size: 1.333em;
}
header {
  border-bottom: 2px solid #343434;
  margin-top: 3em;
  margin-bottom: 1em;
  transition: border 0.2s ease-in-out;
}
header:hover {
  border-bottom: 2px solid rgba(52, 52, 52, 0.6);
}
header::after {
  clear: both;
  content: "";
  display: table;
}
.headerAddress {
  background: #343434;
  font-size: 0.875em;
  left: 0;
  padding: 0.25em 0 0.5em;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 10;
}
.headerAddress p {
  color: #ffffff;
  font-style: none;
  letter-spacing: 0.0125rem;
  margin: 0;
  text-align: center;
}
nav::after {
  clear: both;
  content: "";
  display: table;
}
.logoNav {
  display: inline-block;
  margin-bottom: 1em;
}
.nav_logoText {
  font-size: 1.125em;
  margin-bottom: -0.5em;
  margin-top: 0;
}
.nav_logoImage {
  width: 6.5em;
}
.logoNav:hover {
  cursor: pointer;
}
.siteNav_ul {
  display: inline-block;
  float: right;
  list-style: none;
  padding: 0;
  margin: 0;
}
.nav_site {
  font-size: 0.875em;
  margin-bottom: 0.875em;
}
.siteContent {
  margin: 0 auto 3em auto;
}
.siteContent__singleColumn,
.singleColumn {
  margin: 0 auto;
  max-width: 35em;
}
.cta_container {
  background: url(../images/low-poly.png) no-repeat center center;
  height: 24em;
  position: relative;
}
.cta_tagline {
  font-weight: 700;
  padding: 0 0.125em;
  position: absolute;
  text-align: center;
  top: 1em;
  width: 100%;
}
.cta_serviceLead {
  font-size: 1.125em;
  padding: 0 0.5em;
  position: absolute;
  text-align: center;
  top: 8.5em;
  width: 100%;
}
.cta_action {
  position: absolute;
  text-align: center;
  top: 19.5em;
  width: 100%;
}
figure {
  margin: 1.5em auto;
}
input {
  border: none;
  border-bottom: 2px solid #343434;
}
textarea {
  height: 12em;
  width: 100%;
}
.input__inline {
  display: inline;
}
.scheduleYourInspection {
  margin: 4em 0;
}
.serviceArea {
  margin: 0 auto 4em 0;
}
figcaption {
  font-style: italic;
}
.pricingTable {
  margin: 4em 0;
}
.pricingTable_table {
  width: 100%;
}
tr {
  border: 1px solid #343434;
}
th {
  display: none;
  padding: 0.25em;
  text-align: left;
}
td {
  display: block;
}
td::before {
  content: attr(data-th) ": ";
  display: inline-block;
  font-weight: 700;
  text-align: right;
  margin-right: 5%;
  width: 30%;
}
.pricingTable_table td {
  padding: 0.25em;
}
.pricingNote {
  font-style: italic;
}
.serviceArea_map {
  margin: 0;
}
.nav_definitionList::after {
  clear: both;
  content: "";
  display: table;
}
.definitionList_ul {
  list-style: none;
  margin: 0.5em auto;
  padding: 0;
  width: 50%;
}
.definitionList_ul:nth-child(odd) {
  float: left;
}
.definitionList_ul:nth-child(even) {
  float: right;
}
.definitionList_ul > li {
  margin-bottom: 0.5em;
}
dt {
  font-weight: 700;
  margin-top: 1em;
}
dd {
  border-bottom: 2px solid #343434;
  padding-bottom: 1em;
}
dd:hover {
  border-bottom: 2px solid rgba(52, 52, 52, 0.6);
}
dialog {
  border: 2px solid #343434;
  max-width: 80vw;
}
footer {
  border-top: 2px solid #343434;
  margin-top: 4em;
  padding: 0.5em 1em;
}
footer:hover {
  border-top: 2px solid rgba(52, 52, 52, 0.6);
}
footer::after {
  clear: both;
  content: "";
  display: table;
}
a {
  border-bottom: 2px solid rgba(226, 213, 40, 0.8);
  color: inherit;
  text-decoration: none;
  transition: border 0.2s ease-in-out;
}
a:hover,
a:active,
a:focus {
  border-bottom: 2px solid #3fcc52;
}
a.footnote {
  border-bottom: 0;
  text-decoration: underline;
}
a.footnote:focus,
a.footnote:hover,
a.footnote:active {
  border-bottom: 0;
}
.button {
  background: #e2d528;
  border: 2px solid #343434;
  border-radius: 1px;
  box-shadow: 0 0 0 2px #3fcc52;
  font-weight: 700;
  padding: 0.5em 0.75em;
  transition: border 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}
.button:focus,
.button:hover {
  border: 2px solid #3fcc52;
}
.inspection_form {
  border: 1px solid #343434;
  box-shadow: 1px 1px 0 0 #343434;
  padding: 1em;
}
.form_submit {
  background: #3fcc52;
  box-shadow: 0 0 0 2px #e2d528;
  margin-top: 1em;
  padding: 0.25em 1em;
}
.form_submit:active,
.form_submit:hover {
  border: 2px solid #3fcc52;
  box-shadow: 0 0 0 2px #343434;
}
.site_link__active {
  border-bottom: 2px solid #3fcc52;
}
input {
  transition: border 0.2s ease-in-out;
}
input:hover {
  border-bottom: 2px solid rgba(63, 204, 82, 0.6);
}
input:active,
input:focus {
  border-bottom: 2px solid #3fcc52;
  outline: none;
}
.form_security {
  display: none;
}
.feedback_success {
  color: #32a241;
}
.feedback_warning {
  color: #dca234;
}
.feedback_error {
  color: #ff481a;
}
.disabled {
  text-decoration: line-through;
}
.disabled:hover,
.disabled:active,
.disabled:focus {
  background: white;
}
@media screen and (min-width: 25em) {
  html {
    font: 400 1.125em / 1.5 "Open Sans", sans-serif;
  }
}
@media screen and (min-width: 31.25em) {
  header {
    margin-top: 2em;
  }
}
@media screen and (min-width: 35em) {
  .cta_tagline {
    font-size: 2.5em;
    top: 0.5em;
  }
  .cta_serviceLead {
    top: 8.5em;
  }
  .cta_action {
    top: 18em;
  }
  td,
  th {
    display: table-cell;
  }
  td::before,
  th::before {
    content: none;
  }
  footer > address {
    float: left;
    width: 50%;
  }
  .footer_legal {
    float: right;
    width: 50%;
  }
}
@media screen and (min-width: 40.625em) {
  .site_link {
    margin-right: 2em;
  }
}
@media screen and (min-width: 46.875em) {
  .nav_site {
    display: inline;
  }
}
